import {Component, Prop, Vue} from "vue-property-decorator";

@Component
export default class Home extends Vue {
    // 实时推荐
    // stream：StreamRecs
    public stream: any = [];
    // 离线推荐
    // hot：RateMoreRecentlyProducts
    public hot: any = [];
    // rate：RateMoreProducts
    public rate: any = [];
    // offine：UserRecs
    public offline: any = [];
    // 页面上商品最大展示数量
    public MAX_SHOW_NUM1: number = 5;
    public MAX_SHOW_NUM2: number = 5;
    public MAX_SHOW_NUM3: number = 5;
    public MAX_SHOW_NUM4: number = 5;

    public colors: any = ['#99A9BF', '#F7BA2A', '#FF9900'];

    public bannerList = [
        'https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/52db3dee6f8ccfdf1e73e4a9665b24f9.jpg',
        'https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/63bab54049c48fa65494c16f7e2d60e4.png?w=2452&h=920',
        'https://cdn.cnbj1.fds.api.mi-img.com/product-images/mi11ultra/section1-1.jpg',
        'https://cdn.cnbj1.fds.api.mi-img.com/product-images/shouhuan5/shouhuan5-tab13.jpg',
        'https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/5e896d87c53a449509c14701673098bb.jpg?thumb=1&w=1533&h=575&f=webp&q=90',
        'https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/933683565f5273b4591403daab09212c.jpg?thumb=1&w=1533&h=575&f=webp&q=90'
    ];

    public bannerContent = [
        "手机",
        "电视 盒子",
        "笔记本 显示器",
        "健康 儿童",
        "耳机 音响",
        "生活 配件",
        "书籍 报刊",
    ];

    public sectionPicList = [
        'https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/431e5fd6bfd1b67d096928248be18303.jpg?thumb=1&w=1533&h=150&f=webp&q=90',
        'https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/1c79d1e322e91e187cfc40af08e47977.jpg?thumb=1&w=1533&h=150&f=webp&q=90',
        'https://cdn.cnbj1.fds.api.mi-img.com/mi-mall/89c2a209b742fce9b10d9d196149d634.jpg?thumb=1&w=1533&h=150&f=webp&q=90'
    ]

    public created() {
        this.getRecommendData('/business/rest/product/stream', 0);
        this.getRecommendData('/business/rest/product/hot', 1);
        this.getRecommendData('/business/rest/product/rate', 2);
        this.getRecommendData('/business/rest/product/offline', 3)
    }

    public getRecommendData(url: string, index: number) {
        let name = localStorage.getItem('user');
        this.axios.get(url, {
            params: {
                username: name,
                num: this.MAX_SHOW_NUM1
            }
        }).then(
            (res) => {
                if (res.data.success) {
                    switch (index) {
                        case 0:
                            this.stream = res.data.products;
                            break;
                        case 1:
                            this.hot = res.data.products;
                            break;
                        case 2:
                            this.rate = res.data.products;
                            break;
                        case 3:
                            this.offline = res.data.products;
                            break
                    }
                }
            }
        ).catch(
            (err) => {
                console.log('请求: ' + url + ' 的途中发生错误 ' + err)
            }
        )

    }

    public doMore(index: number) {
        switch (index) {
            case 1:
                this.MAX_SHOW_NUM1 = 19;
                break;
            case 2:
                this.MAX_SHOW_NUM2 = 19;
                break;
            case 3:
                this.MAX_SHOW_NUM3 = 19;
                break;
            case 4:
                this.MAX_SHOW_NUM4 = 19;
                break
        }
    }

    public undoMore(index: number) {
        switch (index) {
            case 1:
                this.MAX_SHOW_NUM1 = 5;
                break;
            case 2:
                this.MAX_SHOW_NUM2 = 5;
                break;
            case 3:
                this.MAX_SHOW_NUM3 = 5;
                break;
            case 4:
                this.MAX_SHOW_NUM4 = 5;
                break
        }
    }

    public async doRate(rate: number, productId: number) {
        // ?score=8&username=abc
        console.log('收到评分数据,productId: ' + productId + " rate: " + rate);
        let user = localStorage.getItem('user');
        let res = await this.axios.get('/business/rest/product/rate/' + productId, {
            params: {
                score: rate,
                username: user
            }
        });
        if (res.data.success == true) {
            this.$alert('评分成功', '提示', {
                confirmButtonText: '确定'
            });
        } else {
            this.$alert('评分失败', '提示', {
                confirmButtonText: '确定'
            });
        }
    }
}
